import React from 'react'
import {useState} from 'react'
import '../static/css/sudoku.css'
let baseSudoku = [
    [1,2,3,4,5,6,7,8,9],
    [4,5,6,7,8,9,1,2,3],
    [7,8,9,1,2,3,4,5,6],
    [2,3,4,5,6,7,8,9,1],
    [5,6,7,8,9,1,2,3,4],
    [8,9,1,2,3,4,5,6,7],
    [3,4,5,6,7,8,9,1,2],
    [6,7,8,9,1,2,3,4,5],
    [9,1,2,3,4,5,6,7,8],
]

function getDeepCopy(arr){
    return JSON.parse(JSON.stringify(arr))
}

function randomInteger(min, max) {
    let rand = min - 0.5 + Math.random() * (max - min + 1);
    return Math.round(rand);
}





function randomizeSudoku(grid) {
    function transposing(grid){
        return grid.map((row,i) => row.map((cell,j) => grid[j][i]))
    }

    function swapRows(grid){
        let area = randomInteger(0,2);
        let line1 = randomInteger(0,2);
        let line2 = randomInteger(0,2);
        while(line1===line2){
            line2 = randomInteger(0,2)
        }
        let n1 = area*3+line1;
        let n2 = area*3+line2;
        let answer = getDeepCopy(grid);
        [answer[n1],answer[n2]]=[answer[n2],answer[n1]];
        return answer
    }

    function swapCols(grid){
        let area = randomInteger(0,2);
        let line1 = randomInteger(0,2);
        let line2 = randomInteger(0,2);
        while(line1===line2){
            line2 = randomInteger(0,2)
        }
        let n1 = area*3+line1;
        let n2 = area*3+line2;
        let answer = getDeepCopy(grid);
        [answer[n1],answer[n2]]=[answer[n2],answer[n1]];
        return answer
    }

    function deleteCells(grid){
        let deletedGrid = getDeepCopy(grid)
        let numCells = randomInteger(30,40);
        for(let i = 0;i<numCells;i++){
            let row = randomInteger(0,8);
            let col = randomInteger(0,8);
            // TODO    
            if(deletedGrid[row][col] === -1){ i--; continue;}
            deletedGrid[row][col] = -1
        }
        return deletedGrid
    }


    return deleteCells(transposing(grid))
}
let randomSudoku = randomizeSudoku(baseSudoku)
let initial = randomSudoku

export default function Sudoku() {
    const [sudokuArr, setSudokuArr] = useState(getDeepCopy(initial))
    function getDeepCopy(arr){
        return JSON.parse(JSON.stringify(arr))
    }
    function onInputChange(e,row,col){
        let val = parseInt(e.target.value) || -1
        let grid = getDeepCopy(sudokuArr)
        if(val === -1 || (val>=1 && val <=9)){
            grid[row][col] = val
        }
        setSudokuArr(grid)
    }
    console.log(initial)
    return (
        <div className="sudoku">
            <table>
                <tbody>
                    {
                        [0,1,2,3,4,5,6,7,8].map((row, rIndex) => {
                            return <tr key={rIndex}>
                                {
                                    [0,1,2,3,4,5,6,7,8].map((col, cIndex) => {
                                        return <td key={rIndex + cIndex}>
                                            <input
                                                onChange={(e)=> {onInputChange(e,row,col)}}
                                                value={sudokuArr[row][col]===-1? '': sudokuArr[row][col]}
                                                disabled={initial[row][col]!==-1}
                                                type="text" />
                                        </td>
                                    })
                                }
                            </tr>
                        })
                    }
                </tbody>
            </table>
        </div>
    )
}
